<template>
  <div class="List">
    <div class="center">
      <h4>取り込んだ曲</h4>
      <hr />
      <play-list :play-list="playList" @change-index="changeIndex" />
    </div>
  </div>
</template>

<script>
import PlayList from '@/components/organisms/PlayList';
import audioPlayer from '@/plugins/AudioPlayer.js';

export default {
  name: 'List',
  components: {
    'play-list': PlayList
  },
  computed: {
    playList() {
      return this.$store.getters['audioPlayer/playList'];
    }
  },
  methods: {
    changeIndex(index) {
      audioPlayer.changeIndex(index);
      this.$router.push('./music');
    }
  }
};
</script>

